
<template>
<div class="home">
            <yl-header 
                :left="left"
                :title="titleName" 
                theme="dark"
                ></yl-header> 
            <yl-div>
                hahah
                <yl-div>hahaha</yl-div>
            </yl-div>
</div>
</template>

<script type="text/babel">
export default {
    data(){
        return {
            titleName:this.$route.name,
            left:{
                text:'',
                icon:'fa  fa-angle-left'
            },
            rowStyleConf:{
                height:'100px',
                backgroundColor:'#000'
            },
            colStyleConf:{
                height:'20px',
                width:'20px',
                backgroundColor:'#fff'
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
.home
    background-color $color-background
    height calc(100% - 48px)
</style>



